import { StockItem, ItemStatus, ActionDefinition } from './types';
import { ColumnConfig } from './StockItemList'; // Import from StockItemList now
// Import necessary MUI Icons
import FlagIcon from '@mui/icons-material/Flag';
import ArchiveIcon from '@mui/icons-material/Archive';
import UnarchiveIcon from '@mui/icons-material/Unarchive';
import ReportProblemIcon from '@mui/icons-material/ReportProblem';
import NoteAddIcon from '@mui/icons-material/NoteAdd';
import EditIcon from '@mui/icons-material/Edit';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import UpdateIcon from '@mui/icons-material/Update';
import VisibilityIcon from '@mui/icons-material/Visibility';
import DoDisturbIcon from '@mui/icons-material/DoDisturb';

// --- Column Configuration ---
export const ALL_COLUMNS: ColumnConfig[] = [
  { id: 'orderDate', label: 'Order Date' },
  { id: 'quantity', label: 'Qty' },
  { id: 'productName', label: 'Product Name' },
  { id: 'deliveryName', label: 'Delivery Name' },
  { id: 'pricePerItem', label: 'Price/Item' },
  { id: 'seller', label: 'Seller/Source' },
  { id: 'destination', label: 'Destination' },
  { id: 'asinSku', label: 'ASIN/SKU' },
  { id: 'purchaseStatus', label: 'Purchase Status' },
  { id: 'orderNumber', label: 'Order #' },
  { id: 'currentStatus', label: 'Status' },
  { id: 'isFlagged', label: 'Flagged' },
  { id: 'acquisitionNotes', label: 'Acquisition Notes' },
  { id: 'issueDescription', label: 'Issue Description' },
  { id: 'dateDelivered', label: 'Date Delivered' },
  { id: 'actions', label: 'Actions' },
];

export const DEFAULT_VISIBLE_COLUMN_IDS: Array<ColumnConfig['id']> = [
  'orderDate', 
  'quantity', 
  'productName', 
  'deliveryName', 
  'currentStatus', 
  'actions',
];

export const LOCAL_STORAGE_KEY_VISIBLE_COLUMNS = 'incomingTool_visibleColumns';

// --- Action Configuration ---

// Define base actions with icons (assigning component type)
const editAction: Partial<ActionDefinition> = { id: 'Edit Item', label: 'Edit', description: 'Edit item details', icon: EditIcon };
const flagAction: Partial<ActionDefinition> = { id: 'Flag Item', label: 'Flag', description: 'Flag this item for attention', icon: FlagIcon };
const unflagAction: Partial<ActionDefinition> = { id: 'Unflag Item', label: 'Unflag', description: 'Remove the flag from this item', icon: DoDisturbIcon };
const archiveAction: Partial<ActionDefinition> = { id: 'Archive', label: 'Archive', description: 'Archive this item', icon: ArchiveIcon };
const unarchiveAction: Partial<ActionDefinition> = { id: 'Unarchive Item', label: 'Unarchive', description: 'Restore this item from archive', icon: UnarchiveIcon };
const reportIssueAction: Partial<ActionDefinition> = { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item', icon: ReportProblemIcon };
const addNoteAction: Partial<ActionDefinition> = { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history', icon: NoteAddIcon };
const markDeliveredAction: Partial<ActionDefinition> = { id: 'Mark as Delivered', label: 'Mark as Delivered', description: 'Update status to Delivered', icon: CheckCircleIcon };
const resolveIssueAction: Partial<ActionDefinition> = { id: 'Resolve Issue', label: 'Resolve Issue', description: 'Mark the issue as resolved', icon: CheckCircleIcon };
const addIssueUpdateAction: Partial<ActionDefinition> = { id: 'Add Issue Update', label: 'Add Issue Update', description: 'Add an update note to the ongoing issue', icon: UpdateIcon };
const viewDetailsAction: Partial<ActionDefinition> = { id: 'View Details', label: 'View Details', description: 'View full item details', icon: VisibilityIcon };

export const getAvailableActions = (status: ItemStatus, isFlagged?: boolean): ActionDefinition[] => {
  let actions: ActionDefinition[] = [];

  // Always include View Details as the first action (if applicable)
  actions.push(viewDetailsAction as ActionDefinition);

  // Determine Flag/Unflag action
  const flagToggleAction = isFlagged ? unflagAction : flagAction;

  switch (status) {
    case 'Pending Delivery':
    case 'Late': // Combine Late and Pending
      actions.push(markDeliveredAction as ActionDefinition);
      actions.push(reportIssueAction as ActionDefinition);
      actions.push(addNoteAction as ActionDefinition);
      actions.push(editAction as ActionDefinition);
      break;
    case 'Delivered':
      actions.push(archiveAction as ActionDefinition);
      actions.push(reportIssueAction as ActionDefinition);
      actions.push(addNoteAction as ActionDefinition);
      actions.push(editAction as ActionDefinition);
      break;
    case 'Issue':
      actions.push(resolveIssueAction as ActionDefinition);
      actions.push(addIssueUpdateAction as ActionDefinition);
      actions.push(archiveAction as ActionDefinition);
      actions.push(addNoteAction as ActionDefinition);
      actions.push(editAction as ActionDefinition);
      break;
    case 'Archived':
      actions.push(unarchiveAction as ActionDefinition);
      actions.push(addNoteAction as ActionDefinition);
      break;
    default:
      actions.push(addNoteAction as ActionDefinition);
      break;
  }

  // Add Flag/Unflag action if not archived
  if (status !== 'Archived') {
    actions.splice(1, 0, flagToggleAction as ActionDefinition);
  }

  return actions;
}; 