import React from 'react';
import {
  AppBar,
  Toolbar,
  TextField,
  Button,
  IconButton,
  Box,
  InputAdornment,
  Tooltip,
  Chip,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import SettingsIcon from '@mui/icons-material/Settings';
import HistoryIcon from '@mui/icons-material/History';
import SearchIcon from '@mui/icons-material/Search';
import { ItemStatus } from './types';

// Define the props interface for ControlSurface
interface ControlSurfaceProps {
  onAddItem: () => void;
  onOpenSettings: () => void;
  onOpenActivity: () => void;
  searchTerm: string;
  onSearchTermChange: (value: string) => void;
  showFlaggedOnly: boolean;
  onShowFlaggedOnlyChange: (checked: boolean) => void;
  currentFilter: ItemStatus | 'All' | 'Late' | 'Archived';
  onCurrentFilterChange: (newFilter: ItemStatus | 'All' | 'Late' | 'Archived') => void;
}

// Define types for filter values including 'Flagged'
// type FilterValue = ItemStatus | 'All' | 'Late' | 'Archived';
// type CombinedFilterValue = FilterValue | 'Flagged';

const ControlSurface: React.FC<ControlSurfaceProps> = ({
  onAddItem,
  onOpenSettings,
  onOpenActivity,
  searchTerm,
  onSearchTermChange,
  showFlaggedOnly,
  onShowFlaggedOnlyChange,
  currentFilter,
  onCurrentFilterChange,
}) => {
  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onSearchTermChange(event.target.value);
  };

  // Define all filter options including 'Flagged'
  const statusFilterOptions: Array<ItemStatus | 'All' | 'Late' | 'Archived'> = [
    'All',
    'Pending Delivery',
    'Delivered',
    'Issue',
    'Late',
    'Archived',
  ];

  // Handler for clicking a status filter chip
  const handleStatusChipClick = (filterValue: ItemStatus | 'All' | 'Late' | 'Archived') => {
    onCurrentFilterChange(filterValue);
  };

  // Handler for clicking the 'Flagged' chip
  const handleFlaggedChipClick = () => {
    onShowFlaggedOnlyChange(!showFlaggedOnly);
  };

  return (
    <AppBar position="static" color="default" elevation={0} sx={{ borderBottom: '1px solid', borderColor: 'divider' }}>
      {/* Use Toolbar for better alignment and structure */}
      <Toolbar disableGutters sx={{ p: 1, display: 'flex', flexWrap: 'wrap', gap: 1.5, alignItems: 'center' }}>
        {/* Top Bar Actions (Aligned Left) */}
        <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
          <Button
            variant="contained"
            startIcon={<AddIcon />}
            onClick={onAddItem}
            size="medium" // Ensure consistent button size
          >
            Add Item
          </Button>
          <Tooltip title="Table Settings">
            <IconButton size="medium" onClick={onOpenSettings} aria-label="Table Settings">
              <SettingsIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="User Activity Log">
            <IconButton size="medium" onClick={onOpenActivity} aria-label="User Activity Log">
              <HistoryIcon />
            </IconButton>
          </Tooltip>
        </Box>

        {/* Spacer - Adjust as needed or remove if middle alignment is desired */}
        {/* <Box sx={{ flexGrow: 1 }} /> */}

        {/* Search Field (Center or Right aligned group) */}
        <TextField
          variant="outlined"
          size="small"
          placeholder="Search Items"
          value={searchTerm}
          onChange={handleSearchChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon color="action" fontSize="small"/>
              </InputAdornment>
            ),
          }}
          sx={{ minWidth: '250px', maxWidth: '400px', flexGrow: { xs: 1, md: 0 } }} // Allow grow on small screens
        />

        {/* Filter Chips Area */}
        <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', alignItems: 'center', flexGrow: 1, justifyContent: { xs: 'flex-start', md: 'flex-end'} }}>
          {/* Status Filter Chips */}
          {statusFilterOptions.map((filter) => {
            const isSelected = currentFilter === filter;
            return (
              <Chip
                key={filter}
                label={filter}
                size="small"
                clickable
                variant={isSelected ? 'filled' : 'outlined'}
                color={isSelected ? 'primary' : 'default'}
                onClick={() => handleStatusChipClick(filter)}
                // Optional: Add icon to selected chip
                // icon={isSelected ? <CheckCircleOutlineIcon fontSize="small" /> : undefined}
                sx={{ fontWeight: isSelected ? 600 : 400 }} // Slightly bolder selected chip
              />
            );
          })}

          {/* Flagged Filter Chip */}
          <Chip
            key="flagged"
            label="Flagged"
            size="small"
            clickable
            variant={showFlaggedOnly ? 'filled' : 'outlined'}
            color={showFlaggedOnly ? 'secondary' : 'default'} // Use secondary color for flagged?
            onClick={handleFlaggedChipClick}
            // Optional: Add icon
            // icon={showFlaggedOnly ? <FlagIcon fontSize="small" /> : undefined}
            sx={{ fontWeight: showFlaggedOnly ? 600 : 400 }}
          />
        </Box>

        {/* Remove the Box with Tabs */}
      </Toolbar>
    </AppBar>
  );
};

export default ControlSurface; 