import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  FormControlLabel,
  Checkbox,
  MenuItem,
} from '@mui/material';
import { StockItem, PurchaseStatus, Destination } from './types';

interface AddItemFormProps {
  isOpen: boolean;
  onSave: (newItemOrUpdate: Omit<StockItem, 'id' | 'currentStatus' | 'dateDelivered' | 'processorNotes' | 'issueDescription'> & { isFlagged: boolean }) => void;
  onClose: () => void;
  initialData?: StockItem | null;
  firstInputRef?: React.RefObject<HTMLInputElement | null>;
}

const AddItemForm: React.FC<AddItemFormProps> = ({ isOpen, onSave, onClose, initialData, firstInputRef }) => {
  const isEditing = !!initialData;

  // Initialize state for each form field
  const [purchaseStatus, setPurchaseStatus] = useState<PurchaseStatus>(initialData?.purchaseStatus || 'Purchased');
  const [deliveryName, setDeliveryName] = useState(initialData?.deliveryName || '');
  const [productName, setProductName] = useState(initialData?.productName || '');
  const [quantity, setQuantity] = useState<number | ''>(initialData?.quantity || '');
  const [pricePerItem, setPricePerItem] = useState<number | ''>(initialData?.pricePerItem || '');
  const [orderNumber, setOrderNumber] = useState(initialData?.orderNumber || '');
  const [orderDate, setOrderDate] = useState(() => (initialData?.orderDate || new Date().toISOString().split('T')[0]));
  const [seller, setSeller] = useState(initialData?.seller || '');
  const [isVatRegistered, setIsVatRegistered] = useState<'Yes' | 'No' | 'Unknown'>(initialData?.isVatRegistered || 'Unknown');
  const [destination, setDestination] = useState<Destination>(initialData?.destination || '');
  const [asinSku, setAsinSku] = useState(initialData?.asinSku || '');
  const [acquisitionNotes, setAcquisitionNotes] = useState(initialData?.acquisitionNotes || '');
  const [isFlagged, setIsFlagged] = useState(initialData?.isFlagged || false);

  const [errors, setErrors] = useState<Record<string, string>>({});

  // Populate state if initialData changes (needed if modal reuses component instance)
  useEffect(() => {
    if (initialData) {
      setPurchaseStatus(initialData.purchaseStatus || 'Purchased');
      setDeliveryName(initialData.deliveryName || '');
      setProductName(initialData.productName || '');
      setQuantity(initialData.quantity || '');
      setPricePerItem(initialData.pricePerItem || '');
      setOrderNumber(initialData.orderNumber || '');
      setOrderDate(initialData.orderDate || new Date().toISOString().split('T')[0]);
      setSeller(initialData.seller || '');
      setIsVatRegistered(initialData.isVatRegistered || 'Unknown');
      setDestination(initialData.destination || '');
      setAsinSku(initialData.asinSku || '');
      setAcquisitionNotes(initialData.acquisitionNotes || '');
      setIsFlagged(initialData.isFlagged || false);
      setErrors({}); // Clear errors when loading new data
    } else {
      // Reset form if initialData becomes null (e.g., switching from edit to add)
      // Optional: Add logic here to reset all fields if needed
    }
  }, [initialData]); // Re-run effect if initialData changes

  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {};
    if (!deliveryName.trim()) newErrors.deliveryName = 'Delivery Name is required.';
    if (!productName.trim()) newErrors.productName = 'Product Name is required.';
    if (quantity === '' || quantity <= 0) newErrors.quantity = 'Quantity must be a positive number.';
    if (pricePerItem === '' || pricePerItem < 0) newErrors.pricePerItem = 'Price must be zero or positive.';
    if (!orderDate) newErrors.orderDate = 'Order Date is required.';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    console.log('AddItemForm: handleSubmit', { deliveryName, productName, quantity, pricePerItem, orderDate, seller, destination, acquisitionNotes, isFlagged });
    const isValid = validateForm();
    console.log('AddItemForm: validation result', isValid, errors);
    if (isValid) {
      console.log('AddItemForm: form valid, calling onSave');
      onSave({
        purchaseStatus,
        deliveryName,
        productName,
        quantity: Number(quantity),
        pricePerItem: Number(pricePerItem),
        orderNumber: orderNumber || undefined,
        orderDate,
        seller: seller || undefined,
        isVatRegistered: isVatRegistered === 'Unknown' ? undefined : isVatRegistered,
        destination: destination || undefined,
        asinSku: asinSku || undefined,
        acquisitionNotes: acquisitionNotes || undefined,
        isFlagged: isFlagged,
      });
    } else {
      console.log('AddItemForm: form invalid, errors:', errors);
    }
  };

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle>{isEditing ? 'Edit Stock Item' : 'Add New Stock Item'}</DialogTitle>
      <DialogContent>
        <Box component="form" onSubmit={handleSubmit} id="add-item-form" noValidate sx={{ mt: 1 }}>
          <TextField
            inputRef={firstInputRef}
            required
            fullWidth
            id="deliveryName"
            label="Delivery Name"
            name="deliveryName"
            value={deliveryName}
            onChange={(e) => setDeliveryName(e.target.value)}
            error={!!errors.deliveryName}
            helperText={errors.deliveryName}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          />

          <TextField
            required
            fullWidth
            id="productName"
            label="Product Name"
            name="productName"
            value={productName}
            onChange={(e) => setProductName(e.target.value)}
            error={!!errors.productName}
            helperText={errors.productName}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          />

          <TextField
            required
            fullWidth
            id="quantity"
            label="Quantity"
            name="quantity"
            type="number"
            value={quantity}
            onChange={(e) => setQuantity(e.target.value === '' ? '' : Number(e.target.value))}
            error={!!errors.quantity}
            helperText={errors.quantity}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
            InputProps={{
              inputProps: { min: 1 },
            }}
          />

          <TextField
            required
            fullWidth
            id="pricePerItem"
            label="Price Per Item"
            name="pricePerItem"
            type="number"
            value={pricePerItem}
            onChange={(e) => setPricePerItem(e.target.value === '' ? '' : Number(e.target.value))}
            error={!!errors.pricePerItem}
            helperText={errors.pricePerItem}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
            InputProps={{
              inputProps: { min: 0 },
            }}
          />

          <TextField
            required
            fullWidth
            id="orderDate"
            label="Order Date"
            name="orderDate"
            type="date"
            value={orderDate}
            onChange={(e) => setOrderDate(e.target.value)}
            error={!!errors.orderDate}
            helperText={errors.orderDate}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
            InputLabelProps={{
              shrink: true,
            }}
          />

          <TextField
            select
            fullWidth
            id="purchaseStatus"
            label="Purchase Status"
            name="purchaseStatus"
            value={purchaseStatus}
            onChange={(e) => setPurchaseStatus(e.target.value as PurchaseStatus)}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
            SelectProps={{
            }}
          >
            <MenuItem value="Purchased">Purchased</MenuItem>
            <MenuItem value="Ordered">Ordered</MenuItem>
            <MenuItem value="Return Expected">Return Expected</MenuItem>
          </TextField>

          <TextField
            fullWidth
            id="orderNumber"
            label="Order Number"
            name="orderNumber"
            value={orderNumber}
            onChange={(e) => setOrderNumber(e.target.value)}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          />

          <TextField
            fullWidth
            id="seller"
            label="Seller / Source"
            name="seller"
            value={seller}
            onChange={(e) => setSeller(e.target.value)}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          />

          <TextField
            select
            fullWidth
            id="isVatRegistered"
            label="Seller VAT Registered?"
            name="isVatRegistered"
            value={isVatRegistered}
            onChange={(e) => setIsVatRegistered(e.target.value as 'Yes' | 'No' | 'Unknown')}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          >
            <MenuItem value="Unknown">Unknown</MenuItem>
            <MenuItem value="Yes">Yes</MenuItem>
            <MenuItem value="No">No</MenuItem>
          </TextField>

          <TextField
            select
            fullWidth
            id="destination"
            label="Planned Destination"
            name="destination"
            value={destination}
            onChange={(e) => setDestination(e.target.value)}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          >
            <MenuItem value="">_Not Set_</MenuItem>
            <MenuItem value="FBA Prep">FBA Prep</MenuItem>
            <MenuItem value="Local Stock Shelf A">Local Stock Shelf A</MenuItem>
            <MenuItem value="Refurbish Pile">Refurbish Pile</MenuItem>
            <MenuItem value="Return to Supplier">Return to Supplier</MenuItem>
          </TextField>

          <TextField
            fullWidth
            id="asinSku"
            label="ASIN / SKU"
            name="asinSku"
            value={asinSku}
            onChange={(e) => setAsinSku(e.target.value)}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          />

          <TextField
            fullWidth
            multiline
            rows={3}
            id="acquisitionNotes"
            label="Acquisition Notes"
            name="acquisitionNotes"
            value={acquisitionNotes}
            onChange={(e) => setAcquisitionNotes(e.target.value)}
            variant="outlined"
            size="small"
            sx={{ mb: 2 }}
          />

          <FormControlLabel
            control={
              <Checkbox
                checked={isFlagged}
                onChange={(e) => setIsFlagged(e.target.checked)}
                name="isFlagged"
                color="secondary"
              />
            }
            label="Flag this item for attention"
            sx={{ mb: 1 }}
          />
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Cancel</Button>
        <Button type="submit" form="add-item-form" variant="contained" onClick={handleSubmit}>
          {isEditing ? 'Save Changes' : 'Add Item'}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AddItemForm;