import React, { useState, useEffect } from 'react';
import { getFirestore, collection, query, where, orderBy, onSnapshot, Timestamp } from 'firebase/firestore';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  Box,
  List,
  ListItem,
  ListItemText,
  CircularProgress,
  Alert,
  Stack,
  useTheme,
  Divider,
} from '@mui/material';
import { StockItem, ActivityEvent, ActionDefinition, LogActionData, ActivityEventType } from './types';
import { getAvailableActions } from './config'; // For action buttons

// Helper function to format optional fields
const formatValue = (value: string | number | undefined | null, theme: any, prefix = '', suffix = '') => {
  if (value === undefined || value === null || value === '') {
    return <Typography component="span" sx={{ color: theme.palette.text.disabled }}>N/A</Typography>; // Indicate if not available
  }
  return `${prefix}${value}${suffix}`;
};

// Helper function to format an activity event into a readable string
const formatActivityEvent = (event: ActivityEvent): string => {
  const time = new Date(event.timestamp).toLocaleString();
  switch (event.type) {
    case 'CREATED':
      return `${time}: Item Created`;
    case 'EDITED':
      const fields = event.details.changedFields?.join(', ') || 'details';
      return `${time}: Item Edited (${fields} changed)`;
    case 'STATUS_CHANGED':
      return `${time}: Status changed from ${event.details.previousStatus || '?'} to ${event.details.newStatus || '?'}`;
    case 'FLAG_TOGGLED':
      return `${time}: Item ${event.details.isFlagged ? 'Flagged' : 'Unflagged'}`;
    case 'ISSUE_REPORTED':
      return `${time}: Issue Reported: ${event.details.issueDescription || ''}`;
    case 'ISSUE_UPDATE_ADDED':
      return `${time}: Issue Update Added: ${event.details.note || ''}`;
    case 'ISSUE_RESOLVED':
      return `${time}: Issue Resolved (Outcome: ${event.details.resolutionOutcome || 'N/A'}${event.details.note ? ` - Note: ${event.details.note}` : ''}`;
    case 'NOTE_ADDED': // For general notes in future
      return `${time}: Note Added: ${event.details.note || ''}`;
    default:
      return `${time}: Unknown action`;
  }
};

// Helper function to map LogActionData from Firestore to the local ActivityEvent format
const mapLogToAction = (log: LogActionData): ActivityEvent | null => {
  // Handle Firestore Timestamp or ISO string
  const timestamp = log.timestamp instanceof Timestamp
    ? log.timestamp.toDate().toISOString()
    : (typeof log.timestamp === 'string' ? log.timestamp : new Date().toISOString());

  const userId = log.userId || 'system'; // Default if userId is missing

  let type: ActivityEventType | null = null;
  let details: ActivityEvent['details'] = {};

  // Map central log action strings to local ActivityEventType
  switch (log.action) {
    case 'item.create':
      type = 'CREATED';
      break;
    case 'item.update.fields':
      type = 'EDITED';
      // Attempt to get changed fields by comparing before/after, fallback to details or generic message
      const changed = log.before && log.after
        ? Object.keys(log.after).filter(key => log.before && log.after && JSON.stringify(log.before[key]) !== JSON.stringify(log.after[key]))
        : (log.details?.changedFields || ['details']);
      details = { changedFields: changed };
      break;
    case 'item.update.status.Delivered':
      type = 'STATUS_CHANGED';
      details = { previousStatus: log.before?.currentStatus, newStatus: 'Delivered' };
      break;
    case 'item.update.status.Archived':
      type = 'STATUS_CHANGED';
      details = { previousStatus: log.before?.currentStatus, newStatus: 'Archived' };
      break;
    // TODO: Add mappings for other specific status changes if implemented (e.g., 'Pending Delivery', 'Issue')
    case 'item.update.flag':
      type = 'FLAG_TOGGLED';
      details = { isFlagged: log.after?.isFlagged };
      break;
    case 'item.update.issue.report':
      type = 'ISSUE_REPORTED';
      details = { issueDescription: log.details?.description || log.details?.issueDescription }; // Allow for variations
      break;
    case 'item.update.issue.resolve':
      type = 'ISSUE_RESOLVED';
      details = { resolutionOutcome: log.details?.outcome, note: log.details?.note };
      break;
    case 'item.update.issue.update': // Action name used in handleSaveIssueUpdate via updateItemAndLog
      type = 'ISSUE_UPDATE_ADDED';
      details = { note: log.details?.note };
      break;
    case 'item.add_note': // Action name used in handleSaveNote via updateItemAndLog
      type = 'NOTE_ADDED';
      details = { note: log.details?.note };
      break;
    // Add more mappings if other log.action values exist
    default:
      console.warn(`Unhandled log action type for UI mapping: ${log.action}`);
      return null; // Don't display unmapped actions for now
  }

  if (!type) return null;

  return {
    timestamp,
    userId,
    // userName: // Optional: Could fetch/join user name later if needed
    type,
    details
  };
};

interface ItemDetailModalProps {
  isOpen: boolean;
  item: StockItem;
  orgId: string;
  onClose: () => void;
  closeButtonRef?: React.RefObject<HTMLButtonElement | null>;
  onActionSelected: (itemId: string, action: ActionDefinition) => void;
}

const ItemDetailModal: React.FC<ItemDetailModalProps> = ({ isOpen, item, orgId, onClose, closeButtonRef, onActionSelected }) => {
  // State for fetched logs
  const [activityEvents, setActivityEvents] = useState<ActivityEvent[]>([]);
  const [loadingLogs, setLoadingLogs] = useState<boolean>(true);
  const [logError, setLogError] = useState<string | null>(null);

  // Actions toolbar
  const availableActions = getAvailableActions(item.currentStatus, item.isFlagged);

  // Get theme here
  const theme = useTheme();

  // Fetch logs when the modal opens or item/orgId changes
  useEffect(() => {
    if (!item?.id || !orgId) { // Ensure we have item ID and orgId prop
      setLoadingLogs(false);
      setLogError("Missing item or organization ID for fetching logs.");
      return;
    }

    setActivityEvents([]); // Clear previous logs
    setLoadingLogs(true);
    setLogError(null);
    const db = getFirestore();
    const entityPath = `stockItems/${item.id}`;
    // Query the 'logs' collection using the orgId prop
    const logsRef = collection(db, `orgs/${orgId}/logs`);
    const q = query(logsRef, where('entityPath', '==', entityPath), orderBy('timestamp', 'desc'));

    const unsubscribe = onSnapshot(q, (snapshot) => {
      // Map the document data directly to LogActionData, assuming data matches the interface
      const fetchedLogs = snapshot.docs.map(doc => ({ ...doc.data() } as LogActionData));

      // Map LogActionData to ActivityEvent for display
      const mappedEvents = fetchedLogs
        .map(mapLogToAction)
        .filter((event): event is ActivityEvent => event !== null); // Filter out nulls and type guard

      setActivityEvents(mappedEvents);
      setLoadingLogs(false);
    }, (error) => {
      console.error("Error fetching logs: ", error);
      setLogError(`Failed to fetch activity log: ${error.message}`);
      setLoadingLogs(false);
    });

    // Cleanup listener on unmount or when item changes
    return () => unsubscribe();
  }, [item?.id, orgId]); // Re-run if item ID or orgId prop changes

  return (
    <Dialog open={isOpen} onClose={onClose} maxWidth="md" fullWidth scroll="paper">
      <DialogTitle>
        {item.productName} - Details
      </DialogTitle>
      <DialogContent dividers={true}> {/* dividers add padding and lines */}
        {/* Action Buttons Toolbar */}
        <Box sx={{ mb: 2, borderBottom: 1, borderColor: 'divider', pb: 2 }}>
          <Typography variant="subtitle2" gutterBottom sx={{ fontWeight: 'bold' }}>Actions</Typography>
          <Stack direction="row" spacing={1} flexWrap="wrap"> {/* Use Stack for layout */}
            {availableActions.map(action => {
              const IconComponent = action.icon; // Get icon component type
              return (
                <Button
                  key={action.id}
                  variant="outlined" // Use outlined for secondary actions in modal
                  size="small"
                  onClick={() => onActionSelected(item.id, action)}
                  startIcon={IconComponent ? React.createElement(IconComponent, { fontSize: "inherit" }) : undefined}
                >
                  {action.label}
                </Button>
              );
            })}
          </Stack>
        </Box>

        {/* Item Details Section */}
        <Box sx={{ mb: 2, borderBottom: 1, borderColor: 'divider', pb: 2 }}>
            <Typography variant="subtitle2" gutterBottom sx={{ fontWeight: 'bold' }}>Details</Typography>
             <Typography variant="body2">ID: {formatValue(item.id, theme)}</Typography>
             <Typography variant="body2">Delivery Name: {formatValue(item.deliveryName, theme)}</Typography>
             <Typography variant="body2">Product Name: {formatValue(item.productName, theme)}</Typography>
             <Typography variant="body2">Quantity: {formatValue(item.quantity, theme)}</Typography>
             <Typography variant="body2">Price/Item: {formatValue(item.pricePerItem, theme, '$')}</Typography> {/* Assuming USD */}
             <Typography variant="body2">Order #: {formatValue(item.orderNumber, theme)}</Typography>
             <Typography variant="body2">Order Date: {formatValue(item.orderDate ? new Date(item.orderDate).toLocaleDateString() : null, theme)}</Typography>
             <Typography variant="body2">Seller: {formatValue(item.seller, theme)}</Typography>
             <Typography variant="body2">Purchase Status: {formatValue(item.purchaseStatus, theme)}</Typography>
             <Typography variant="body2">Current Status: {formatValue(item.currentStatus, theme)}</Typography>
             <Typography variant="body2">Date Delivered: {formatValue(item.dateDelivered ? new Date(item.dateDelivered).toLocaleDateString() : null, theme)}</Typography>
             <Typography variant="body2">Destination: {formatValue(item.destination, theme)}</Typography>
             <Typography variant="body2">ASIN/SKU: {formatValue(item.asinSku, theme)}</Typography>
             <Typography variant="body2">Flagged: {formatValue(item.isFlagged ? 'Yes' : 'No', theme)}</Typography>
             <Typography variant="body2">VAT Registered: {formatValue(item.isVatRegistered, theme)}</Typography>
             <Typography variant="body2">Acquisition Notes: {formatValue(item.acquisitionNotes, theme)}</Typography>
             <Typography variant="body2">Processor Notes: {formatValue(item.processorNotes, theme)}</Typography>
             {item.currentStatus === 'Issue' && (
               <Typography variant="body2" color="error">Issue Description: {formatValue(item.issueDescription, theme)}</Typography>
             )}
         </Box>

        {/* Activity Log Section */}
        <Box>
          <Typography variant="subtitle2" gutterBottom sx={{ fontWeight: 'bold' }}>Activity Log</Typography>
          {loadingLogs ? (
            <CircularProgress size={24} />
          ) : logError ? (
            <Alert severity="error">{logError}</Alert>
          ) : activityEvents.length > 0 ? (
            <List dense disablePadding>
              {activityEvents.map((event, index) => (
                <React.Fragment key={`${event.timestamp}-${index}`}> 
                  <ListItem disableGutters sx={{ py: 0.5 }}>
                    <ListItemText 
                      primary={formatActivityEvent(event)} 
                      primaryTypographyProps={{ variant: 'body2' }}
                      secondary={`User: ${event.userId}`}
                      secondaryTypographyProps={{ variant: 'caption' }}
                    />
                  </ListItem>
                  {index < activityEvents.length - 1 && <Divider component="li" variant="inset" />}
                </React.Fragment>
              ))}
            </List>
          ) : (
            <Typography variant="body2" color="text.secondary">No activity recorded.</Typography>
          )}
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} ref={closeButtonRef}>Close</Button>
      </DialogActions>
    </Dialog>
  );
};

export default ItemDetailModal;