import React from 'react';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  useSortable,
  horizontalListSortingStrategy,
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { restrictToHorizontalAxis } from '@dnd-kit/modifiers';
import { StockItem, ItemStatus } from './types'; // Corrected type import
import {
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  IconButton,
  Card,
  CardContent,
  Chip, // Import Chip
} from '@mui/material';
import { Theme, useTheme } from '@mui/material/styles';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import FlagIcon from '@mui/icons-material/Flag'; // Ensure FlagIcon is imported
import { isItemLate } from './utils'; // Import isItemLate from utils

// --- Helper Function for Status Chip --- (Consider moving to utils.ts later)
type ChipColor = 'default' | 'primary' | 'secondary' | 'error' | 'info' | 'success' | 'warning';
interface StatusChipProps {
  color: ChipColor;
  variant: 'filled' | 'outlined';
}

const getStatusChipProps = (status: ItemStatus): StatusChipProps => {
  switch (status) {
    case 'Delivered':
      return { color: 'success', variant: 'filled' };
    case 'Issue':
      return { color: 'error', variant: 'filled' };
    case 'Late':
      return { color: 'warning', variant: 'filled' };
    case 'Pending Delivery':
      return { color: 'info', variant: 'outlined' };
    case 'Archived':
      return { color: 'default', variant: 'outlined' };
    default:
      return { color: 'default', variant: 'outlined' };
  }
};

// --- Reused Interfaces/Types ---
export interface ColumnConfig {
  id: keyof StockItem | 'actions';
  label: string;
}

// --- Draggable Header Component ---
interface DraggableHeaderProps {
  column: ColumnConfig;
}

const DraggableHeader: React.FC<DraggableHeaderProps> = ({ column }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: column.id });

  const style: React.CSSProperties = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
    cursor: 'grab',
    touchAction: 'none',
  };

  const isNumeric = ['quantity', 'pricePerItem'].includes(column.id);

  return (
    <TableCell
      ref={setNodeRef}
      style={style}
      {...attributes}
      {...listeners}
      align={isNumeric ? 'right' : 'left'}
      padding={column.id === 'actions' ? 'checkbox' : 'normal'} // Apply checkbox padding for actions column
      sx={{
        whiteSpace: 'nowrap',
        // Removed bgcolor: isDragging - Handled by theme/MUI defaults ideally
        // Removed specific width settings for now, rely on content or Table layout
      }}
    >
      {column.label}
    </TableCell>
  );
};

// --- Mobile Item Card Component ---
interface MobileItemCardProps {
  item: StockItem;
  onViewDetails: (itemId: string) => void;
  onActionClick: (event: React.MouseEvent<HTMLElement>, item: StockItem) => void;
  theme: Theme;
}

const MobileItemCard: React.FC<MobileItemCardProps> = ({ item, onViewDetails, onActionClick, theme }) => {
  // Basic mobile card remains, using new status chip
  const statusProps = getStatusChipProps(item.currentStatus);
  return (
    <Card
      variant="outlined"
      sx={{ mb: 1, cursor: 'pointer', '&:hover': { bgcolor: 'action.hover' } }}
      onClick={() => onViewDetails(item.id)}
    >
      <CardContent sx={{ pb: '8px !important', position: 'relative' }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
          <Box sx={{ flexGrow: 1, mr: 1, display: 'flex', alignItems: 'center', gap: 0.5 }}>
             {item.isFlagged && <FlagIcon sx={{ color: 'secondary.main', fontSize: '1rem' }} />}
             <Typography variant="subtitle1" component="div" sx={{ fontWeight: 'bold', lineHeight: 1.3 }}>
              {item.productName || item.deliveryName || 'Unnamed Item'}
            </Typography>
          </Box>
          <IconButton size="small" onClick={(e) => { e.stopPropagation(); onActionClick(e, item); }} sx={{ position: 'absolute', top: 4, right: 4 }}>
            <MoreVertIcon />
          </IconButton>
        </Box>
        <Chip label={item.currentStatus} size="small" color={statusProps.color} variant={statusProps.variant} sx={{ mr: 1, mt: 0.5, mb: 0.5 }}/>
        <Typography variant="body2" color="text.secondary" display="inline">
          Qty: {item.quantity ?? 'N/A'} | Seller: {item.seller || 'N/A'}
        </Typography>
        <Typography variant="body2" color="text.secondary" display="block" sx={{ fontSize: '0.8rem' }}>
          Order Date: {item.orderDate ? new Date(item.orderDate).toLocaleDateString() : 'N/A'}
        </Typography>
      </CardContent>
    </Card>
  );
};

// --- StockItemList Component ---
interface StockItemListProps {
  items: StockItem[];
  loading: boolean;
  isMobile: boolean;
  visibleColumns: ColumnConfig[];
  visibleColumnIds: Array<ColumnConfig['id']>;
  onColumnOrderChange: (newColumnIds: Array<ColumnConfig['id']>) => void;
  onViewDetails: (itemId: string) => void;
  onMenuOpen: (event: React.MouseEvent<HTMLElement>, item: StockItem) => void;
}

const StockItemList: React.FC<StockItemListProps> = ({
  items,
  loading,
  isMobile,
  visibleColumns,
  visibleColumnIds,
  onColumnOrderChange,
  onViewDetails,
  onMenuOpen
}) => {
  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (over && active.id !== over.id) {
      const oldIndex = visibleColumnIds.indexOf(active.id as (keyof StockItem | 'actions'));
      const newIndex = visibleColumnIds.indexOf(over.id as (keyof StockItem | 'actions'));
      if (oldIndex === -1 || newIndex === -1) {
        console.error('Dragged item ID not found in visible columns', { activeId: active.id, overId: over.id });
        return;
      }
      const newOrder = arrayMove(visibleColumnIds, oldIndex, newIndex);
      onColumnOrderChange(newOrder);
    }
  };

  const theme = useTheme();

  // --- Render Logic ---
  if (loading) {
    return <Typography>Loading items...</Typography>; // Add Skeleton later
  }

  // Mobile View
  if (isMobile) {
    return (
      <Box sx={{ pt: 1 }}> {/* Add padding top */} 
        {items.map((item) => (
            <MobileItemCard
              key={item.id}
              item={item}
              onViewDetails={onViewDetails}
              onActionClick={onMenuOpen}
              theme={theme}
            />
        ))}
        {items.length === 0 && !loading && (
           <Typography align="center" sx={{ mt: 4, color: 'text.secondary' }}>
             No items match the current filters.
           </Typography>
         )}
      </Box>
    );
  }

  // Desktop View (Table)
  return (
    <DndContext
      sensors={sensors}
      collisionDetection={closestCenter}
      onDragEnd={handleDragEnd}
      modifiers={[restrictToHorizontalAxis]}
    >
      <TableContainer component={Paper} elevation={1} sx={{ mt: 2 }}> {/* Add margin top */}
        <Table size="small">
          <TableHead sx={{ bgcolor: 'grey.100' }}> {/* Apply Head Styling */}
            <TableRow>
              <SortableContext items={visibleColumnIds} strategy={horizontalListSortingStrategy}>
                {visibleColumns.map((col) => (
                  <DraggableHeader key={col.id} column={col} />
                ))}
              </SortableContext>
            </TableRow>
          </TableHead>
          <TableBody>
            {items.map((item) => {
              const isLate = isItemLate(item); // Check if late
              const statusProps = getStatusChipProps(item.currentStatus); // Get status chip props

              return (
                <TableRow
                  key={item.id}
                  hover
                  onClick={() => onViewDetails(item.id)}
                  sx={{
                    cursor: 'pointer',
                    bgcolor: 'background.paper', // Remove background logic
                    '&:hover': { bgcolor: 'action.hover' }, // Standard hover
                    '&:last-child td, &:last-child th': { border: 0 }, // Remove last border
                    ...(isLate && { borderLeft: 3, borderColor: 'warning.main' }), // Apply late indicator
                  }}
                >
                  {visibleColumns.map((column) => {
                    const cellValue = item[column.id as keyof StockItem] ?? '';
                    const isNumeric = ['quantity', 'pricePerItem'].includes(column.id);

                    return (
                      <TableCell
                        key={column.id}
                        align={isNumeric ? 'right' : 'left'}
                        padding={column.id === 'actions' ? 'checkbox' : 'normal'}
                        sx={{
                          whiteSpace: 'nowrap',
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                        }}
                      >
                        {column.id === 'actions' ? (
                          <IconButton size="small" onClick={(e) => { e.stopPropagation(); onMenuOpen(e, item); }} color="inherit">
                            <MoreVertIcon fontSize="small" />
                          </IconButton>
                        ) : column.id === 'currentStatus' ? (
                          <Chip label={item.currentStatus} size="small" color={statusProps.color} variant={statusProps.variant} />
                        ) : column.id === 'isFlagged' ? (
                          item.isFlagged ? <FlagIcon sx={{ fontSize: '1rem', color: 'secondary.main', display:'block', margin:'auto' }} /> : null
                        ) : column.id === 'orderDate' || column.id === 'dateDelivered' ? (
                          cellValue ? new Date(cellValue as string).toLocaleDateString() : ''
                        ) : (
                          ['deliveryName', 'productName'].includes(column.id) ? (
                            <Box component="span" sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                              {item.isFlagged && <FlagIcon sx={{ color: 'secondary.main', fontSize: '1rem' }} />}
                              {cellValue}
                            </Box>
                          ) : (
                            cellValue
                          )
                        )}
                      </TableCell>
                    );
                  })}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
        {items.length === 0 && !loading && (
          <Typography align="center" sx={{ p: 4, color: 'text.secondary' }}>
             No items match the current filters.
           </Typography>
         )}
      </TableContainer>
    </DndContext>
  );
};

export default StockItemList; 